<!-- 【弹幕信息】弹窗页面 -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="bullet" ref="bulletForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="主键id" class="hidden" prop="id">
          <el-input type="hidden" v-model="bullet.id"/>
        </el-form-item>
        <el-form-item label="所属用户" prop="userId">
          <el-select v-model="bullet.userId" placeholder="请选择所属用户" :disabled="dialog.disabled">
            <el-option v-for="register in registerDict" :key="register.id" :value="register.id" :label="register.loginName"/>
          </el-select>
        </el-form-item>
        <el-form-item label="颜色" prop="color">
          <el-input v-model="bullet.color" placeholder="请输入颜色" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="时间" prop="time">
          <el-input v-model="bullet.time" placeholder="请输入时间" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="内容" prop="content">
          <el-input type="textarea" v-model="bullet.content" placeholder="请输入内容" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel">取 消</el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "bulletForm",
    data() {
      return {
        registerDict:[],
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        bullet: {
          id: "",
          userId: "",
          color: "",
          time: "",
          content: ""
        },
        //表单校验
        rules: {
          userId: [{required: true, message: '请选择所属用户', trigger: 'blur'}],
          color: [{required: true, message: '请输入颜色', trigger: 'blur'}],
          time: [{required: true, message: '请输入时间', trigger: 'blur'}],
          content: [{required: true, message: '请输入内容', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //【所属用户】
      getRegisterDict() {
        this.$request.doGet('/admin/register/getList').then(res => {
          if (res.isOk) {
            this.registerDict = res.list;
          }
        })
      },
      //打开弹窗
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getRegisterDict();//加载【所属用户】
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getBullet(option.id);
        }
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false;
      },
      //根据id获取数据
      getBullet(id) {
        this.$request.doGet("/admin/bullet/get?id=" + id).then(res => {
          if (res.isOk) {
            this.bullet = res.obj;
          }
        });
      },
      //提交表单
      submitForm: function () {
        const that = this;
        that.$refs.bulletForm.validate((valid) => {
          if (valid) {//表单校验
            that.$request.doPost('/admin/bullet/sub', that.bullet).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.bulletForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      }
    }
  }
</script>

<style scoped>

</style>



