<!-- 【作品信息】管理页面 -->
<template>
  <div class="body">
    <!-- 表单弹窗 -->
    <works-form ref="worksDialog" @reloadList="getList"/>
    <!-- 查询条件 -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="作品名称" prop="name">
        <el-input v-model="queryForm.name" placeholder="请输入作品名称" size="small" clearable/>
      </el-form-item>
      <el-form-item label="作品类型" prop="type">
        <el-select v-model="queryForm.type" placeholder="请选择作品类型" size="small" clearable>
          <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
<!--      <el-form-item label="关键词" prop="keyWord">-->
<!--        <el-input v-model="queryForm.keyWord" placeholder="请输入关键词" size="small" clearable/>-->
<!--      </el-form-item>-->
<!--      <el-form-item label="价格" prop="price">-->
<!--        <el-input v-model="queryForm.price" placeholder="请输入价格" size="small" clearable/>-->
<!--      </el-form-item>-->
<!--      <el-form-item label="开发工具" prop="tools">-->
<!--        <el-input v-model="queryForm.tools" placeholder="请输入开发工具" size="small" clearable/>-->
<!--      </el-form-item>-->
<!--      <el-form-item label="部署环境" prop="environment">-->
<!--        <el-input v-model="queryForm.environment" placeholder="请输入部署环境" size="small" clearable/>-->
<!--      </el-form-item>-->
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()">重置</el-button>
      </el-form-item>
    </el-form>
    <!-- 查询按钮 -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:'新增作品信息'})" v-hasPermi="['works:add']">新增</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['works:delete']">批量删除</el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!-- 表格区域 -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="序号" type="index" fixed width="50"/>
      <el-table-column label="作品图片" prop="picture" sortable>
        <template slot-scope="scope">
          <el-image :src="formatUrl(scope.row.picture)" class="column-image"></el-image>
        </template>
      </el-table-column>
      <el-table-column label="作品名称" prop="name" sortable/>
      <el-table-column label="作品类型" prop="type" sortable>
        <template slot-scope="scope">
          <span :style="dict.style" v-for="dict in typeDict" v-if="dict.dictValue==scope.row.type">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="关键词" prop="keyWord" sortable/>
      <el-table-column label="价格" prop="price" sortable/>
      <el-table-column label="开发工具" prop="tools" sortable/>
      <el-table-column label="部署环境" prop="environment" sortable/>
      <el-table-column label="基本介绍" prop="introduce" show-overflow-tooltip sortable/>
      <el-table-column label="操作" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'查看作品信息',pageFrom:'view',id:scope.row.id})" v-hasPermi="['works:view']">查看</el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'修改作品信息',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['works:edit']">修改</el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['works:delete']">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页区域 -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import worksForm from './worksForm'
    export default {
      components: {worksForm},
      data() {
        return {
          loading: true,//是否加载完成
          selectedData: [],//勾选数据
          total: 0, //记录数
          dataList: [],
          queryForm: {
            current: 1,
            size: 10,
            picture: "",
            name: "",
            type: "",
            keyWord: "",
            price: "",
            tools: "",
            environment: "",
            introduce: ""
          }
        };
      },
      mounted() {
        //初始化数据
        this.getList();
      },
      computed: {
        //【作品类型】
        typeDict() {
          return this.$store.getters.getDictArray('works_type');
        },
      },
      methods: {
        //点击搜索
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //查询列表数据
        getList() {
          this.$request.doGet('/admin/works/getPage', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              this.dataList = res.obj.records;
              this.total = res.obj.total;
            }
          });
        },
        //重置查询
        resetQuery() {
          this.$refs['queryForm'].resetFields();
        },
        //打开弹窗
        openDialog(option) {
          this.$refs.worksDialog.open(option);
        },
        //选择页数
        handleSizeChange: function (size) {
          this.queryForm.size = size;
          this.getList();
        },
        //选择页码
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
        //获取勾选数据
        handleSelectionChange(data) {
          this.selectedData = data;
        },
        //勾选批量删除
        handleDeleteAll() {
          const that = this;
          that.showConfirm('确定删除选中的数据吗?', function () {
            if (that.selectedData.length > 0) {
              that.selectedData.forEach(function (item, index) {
                that.delete(item.id);
              });
              that.showSuccess("删除成功");
            } else {
              that.showWarn("至少选择一条数据！");
            }
          });
        },
        //选中行删除
        handleDelete(id) {
          const that = this;
          that.showConfirm('确定删除选中的除数据吗?', function () {
            that.delete(id);
            that.showSuccess("删除成功");
          });
        },
        //删除方法
        delete(id) {
          this.$request.doGet('/admin/works/delete?id=' + id).then(res => {
            if (res.isOk) {
              this.getList();
            }
          });
        },
      }
    };
</script>



