<!-- 【卡片信息】弹窗页面 -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="card" ref="cardForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="主键id" class="hidden" prop="id">
          <el-input type="hidden" v-model="card.id"/>
        </el-form-item>
        <el-form-item label="卡号" prop="cardNumber">
          <el-input v-model="card.cardNumber" placeholder="请输入卡号" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="卡片类型" prop="cardType">
          <el-select v-model="card.cardType" placeholder="请选择卡片类型" :disabled="dialog.disabled" >
            <el-option v-for="dict in cardTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="所属用户" prop="userNo">
          <el-select v-model="card.userNo" placeholder="请选择所属用户" :disabled="dialog.disabled">
            <el-option v-for="sysAuthor in sysAuthorDict" :key="sysAuthor.id" :value="sysAuthor.id" :label="sysAuthor.nickName"/>
          </el-select>
        </el-form-item>
        <el-form-item label="状态" prop="cardStatus">
          <el-select v-model="card.cardStatus" placeholder="请选择状态" :disabled="dialog.disabled" >
            <el-option v-for="dict in cardStatusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="价格" prop="cardPrice">
          <el-input v-model="card.cardPrice" placeholder="请输入价格" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="过期时间" prop="cardExpireTime">
          <el-date-picker v-model="card.cardExpireTime" placeholder="请选择过期时间" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
        </el-form-item>
        <el-form-item label="内容" prop="cardContent">
          <el-input type="textarea" v-model="card.cardContent" placeholder="请输入内容" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel">取 消</el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "cardForm",
    data() {
      return {
        sysAuthorDict:[],
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        card: {
          cardNumber: "",
          cardType: "",
          userNo: "",
          cardStatus: "",
          cardPrice: "",
          cardExpireTime: "",
          cardContent: ""
        },
        //表单校验
        rules: {
          cardNumber: [{required: true, message: '请输入卡号', trigger: 'blur'},{validator:this.$validator.unique,form:this,model:'card',tableName:'card',column:'card_number',message:'卡号已存在',trigger:'blur'}],
          cardType: [{required: true, message: '请选择卡片类型', trigger: 'blur'}],
          userNo: [{required: true, message: '请选择所属用户', trigger: 'blur'}],
          cardStatus: [{required: true, message: '请选择状态', trigger: 'blur'}],
          cardPrice: [{required: true, message: '请输入价格', trigger: 'blur'}],
          cardExpireTime: [{required: true, message: '请选择过期时间', trigger: 'blur'}],
          cardContent: [{required: true, message: '请输入内容', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //【卡片类型】
      cardTypeDict() {
        return this.$store.getters.getDictArray('card_type');
      },
      //【状态】
      cardStatusDict() {
        return this.$store.getters.getDictArray('card_status');
      },
    },
    methods: {
      //【所属用户】
      getSysAuthorDict() {
        this.$request.doGet('/admin/sysAuthor/getList').then(res => {
          if (res.isOk) {
            this.sysAuthorDict = res.list;
          }
        })
      },
      //打开弹窗
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getSysAuthorDict();//加载【所属用户】
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getCard(option.id);
        }
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false;
      },
      //根据id获取数据
      getCard(id) {
        this.$request.doGet("/admin/card/get?id=" + id).then(res => {
          if (res.isOk) {
            this.card = res.obj;
          }
        });
      },
      //提交表单
      submitForm: function () {
        const that = this;
        that.$refs.cardForm.validate((valid) => {
          if (valid) {//表单校验
            that.$request.doPost('/admin/card/sub', that.card).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.cardForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      }
    }
  }
</script>

<style scoped>

</style>



