<!-- 【订单信息】弹窗页面 -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="orders" ref="ordersForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="主键id" class="hidden" prop="id">
          <el-input type="hidden" v-model="orders.id"/>
        </el-form-item>
        <el-form-item label="订单编号" prop="orderNo">
          <el-input v-model="orders.orderNo" placeholder="请输入订单编号" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="项目名称" prop="name">
          <el-input v-model="orders.name" placeholder="请输入项目名称" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="订单价格" prop="price">
          <el-input v-model="orders.price" placeholder="请输入订单价格" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="微信号" prop="wxno">
          <el-input v-model="orders.wxno" placeholder="请输入微信号" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="下单时间" prop="orderDate">
          <el-date-picker v-model="orders.orderDate" placeholder="请选择下单时间" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
        </el-form-item>
        <el-form-item label="开发进度" prop="status">
          <el-select v-model="orders.status" placeholder="请选择开发进度" :disabled="dialog.disabled" >
            <el-option v-for="dict in statusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="下单用户" prop="userId">
          <el-select v-model="orders.userId" placeholder="请选择下单用户" :disabled="dialog.disabled">
            <el-option v-for="sysAuthor in sysAuthorDict" :key="sysAuthor.id" :value="sysAuthor.id" :label="sysAuthor.nickName"/>
          </el-select>
        </el-form-item>
        <el-form-item label="订单类型" prop="type">
          <el-select v-model="orders.type" placeholder="请选择订单类型" :disabled="dialog.disabled" >
            <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="内容说明" prop="content">
          <el-input type="textarea" v-model="orders.content" placeholder="请输入内容说明" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="文件列表" prop="fileList">
          <sui-file type="works" v-if="dialog.open" :refId="orders.id" v-model="orders.fileList" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel">取 消</el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "ordersForm",
    data() {
      return {
        sysAuthorDict:[],
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        orders: {
          orderNo: "",
          name: "",
          price: "",
          wxno: "",
          orderDate: "",
          status: "",
          userId: "",
          type: "",
          content: ""
        },
        //表单校验
        rules: {
          orderNo: [{required: true, message: '请输入订单编号', trigger: 'blur'}],
          name: [{required: true, message: '请输入项目名称', trigger: 'blur'}],
          price: [{required: true, message: '请输入订单价格', trigger: 'blur'}],
          wxno: [{required: true, message: '请输入微信号', trigger: 'blur'}],
          orderDate: [{required: true, message: '请选择下单时间', trigger: 'blur'}],
          status: [{required: true, message: '请选择开发进度', trigger: 'blur'}],
          userId: [{required: true, message: '请选择下单用户', trigger: 'blur'}],
          type: [{required: true, message: '请选择订单类型', trigger: 'blur'}],
          content: [{required: true, message: '请输入内容说明', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //【开发进度】
      statusDict() {
        return this.$store.getters.getDictArray('order_status');
      },
      //【订单类型】
      typeDict() {
        return this.$store.getters.getDictArray('order_type');
      },
    },
    methods: {
      //【下单用户】
      getSysAuthorDict() {
        this.$request.doGet('/admin/sysAuthor/getList').then(res => {
          if (res.isOk) {
            this.sysAuthorDict = res.list;
          }
        })
      },
      //打开弹窗
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getSysAuthorDict();//加载【下单用户】
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getOrders(option.id);
        }
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false;
      },
      //根据id获取数据
      getOrders(id) {
        this.$request.doGet("/admin/orders/get?id=" + id).then(res => {
          if (res.isOk) {
            this.orders = res.obj;
          }
        });
      },
      //提交表单
      submitForm: function () {
        const that = this;
        that.$refs.ordersForm.validate((valid) => {
          if (valid) {//表单校验
            that.$request.doPost('/admin/orders/sub', that.orders).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.ordersForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      }
    }
  }
</script>

<style scoped>

</style>



