<!-- 【卡片信息】管理页面 -->
<template>
  <div class="body">
    <!-- 表单弹窗 -->
    <card-form ref="cardDialog" @reloadList="getList"/>
    <!-- 查询条件 -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="卡号" prop="cardNumber">
        <el-input v-model="queryForm.cardNumber" placeholder="请输入卡号" size="small" clearable/>
      </el-form-item>
      <el-form-item label="卡片类型" prop="cardType">
        <el-select v-model="queryForm.cardType" placeholder="请选择卡片类型" size="small" clearable>
          <el-option v-for="dict in cardTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="所属用户" prop="userNo">
        <el-select v-model="queryForm.userNo" placeholder="请选择所属用户" size="small" clearable>
          <el-option v-for="sysAuthor in sysAuthorDict" :key="sysAuthor.unionid" :value="sysAuthor.unionid" :label="sysAuthor.nickName"/>
        </el-select>
      </el-form-item>
      <el-form-item label="状态" prop="cardStatus">
        <el-select v-model="queryForm.cardStatus" placeholder="请选择状态" size="small" clearable>
          <el-option v-for="dict in cardStatusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()">重置</el-button>
      </el-form-item>
    </el-form>
    <!-- 查询按钮 -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:'新增卡片信息'})" v-hasPermi="['card:add']">新增</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['card:delete']">批量删除</el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!-- 表格区域 -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="序号" type="index" fixed width="50"/>
      <el-table-column label="卡号" prop="cardNumber" sortable/>
      <el-table-column label="卡片类型" prop="cardType" sortable>
        <template slot-scope="scope">
          <span :style="dict.style" v-for="dict in cardTypeDict" v-if="dict.dictValue==scope.row.cardType">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="所属用户" prop="userNo" sortable>
        <template slot-scope="scope">
          <span v-for="sysAuthor in sysAuthorDict" v-if="sysAuthor.unionid==scope.row.userNo">{{sysAuthor.nickName}}</span>
        </template>
      </el-table-column>
      <el-table-column label="状态" prop="cardStatus" sortable>
        <template slot-scope="scope">
          <span :style="dict.style" v-for="dict in cardStatusDict" v-if="dict.dictValue==scope.row.cardStatus">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="价格" prop="cardPrice" sortable/>
      <el-table-column label="过期时间" prop="cardExpireTime" sortable/>
      <el-table-column label="内容" prop="cardContent" show-overflow-tooltip sortable/>
      <el-table-column label="操作" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'查看卡片信息',pageFrom:'view',id:scope.row.id})" v-hasPermi="['card:view']">查看</el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'修改卡片信息',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['card:edit']">修改</el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['card:delete']">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页区域 -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import cardForm from './cardForm'
    export default {
      components: {cardForm},
      data() {
        return {
          loading: true,//是否加载完成
          selectedData: [],//勾选数据
          total: 0, //记录数
          sysAuthorDict: [],
          dataList: [],
          queryForm: {
            current: 1,
            size: 10,
            cardNumber: "",
            cardType: "",
            userNo: "",
            cardStatus: "",
            cardPrice: "",
            cardExpireTime: "",
            cardContent: ""
          }
        };
      },
      mounted() {
        //初始化数据
        this.getList();
        //初始化【所属用户】
        this.getSysAuthorDict();
      },
      computed: {
        //【卡片类型】
        cardTypeDict() {
          return this.$store.getters.getDictArray('card_type');
        },
        //【状态】
        cardStatusDict() {
          return this.$store.getters.getDictArray('card_status');
        },
      },
      methods: {
        //点击搜索
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //查询列表数据
        getList() {
          this.$request.doGet('/admin/card/getPage', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              this.dataList = res.obj.records;
              this.total = res.obj.total;
            }
          });
        },
        //【所属用户】
        getSysAuthorDict() {
          this.$request.doGet('/admin/sysAuthor/getList').then(res => {
            if (res.isOk) {
              this.sysAuthorDict = res.list;
            }
          });
        },
        //重置查询
        resetQuery() {
          this.$refs['queryForm'].resetFields();
        },
        //打开弹窗
        openDialog(option) {
          this.$refs.cardDialog.open(option);
        },
        //选择页数
        handleSizeChange: function (size) {
          this.queryForm.size = size;
          this.getList();
        },
        //选择页码
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
        //获取勾选数据
        handleSelectionChange(data) {
          this.selectedData = data;
        },
        //勾选批量删除
        handleDeleteAll() {
          const that = this;
          that.showConfirm('确定删除选中的数据吗?', function () {
            if (that.selectedData.length > 0) {
              that.selectedData.forEach(function (item, index) {
                that.delete(item.id);
              });
              that.showSuccess("删除成功");
            } else {
              that.showWarn("至少选择一条数据！");
            }
          });
        },
        //选中行删除
        handleDelete(id) {
          const that = this;
          that.showConfirm('确定删除选中的除数据吗?', function () {
            that.delete(id);
            that.showSuccess("删除成功");
          });
        },
        //删除方法
        delete(id) {
          this.$request.doGet('/admin/card/delete?id=' + id).then(res => {
            if (res.isOk) {
              this.getList();
            }
          });
        },
      }
    };
</script>



