<!-- 【订单信息】管理页面 -->
<template>
  <div class="body">
    <!-- 表单弹窗 -->
    <orders-form ref="ordersDialog" @reloadList="getList"/>
    <!-- 查询条件 -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="订单编号" prop="orderNo">
        <el-input v-model="queryForm.orderNo" placeholder="请输入订单编号" size="small" clearable/>
      </el-form-item>
      <el-form-item label="项目名称" prop="name">
        <el-input v-model="queryForm.name" placeholder="请输入项目名称" size="small" clearable/>
      </el-form-item>
<!--      <el-form-item label="订单价格" prop="price">-->
<!--        <el-input v-model="queryForm.price" placeholder="请输入订单价格" size="small" clearable/>-->
<!--      </el-form-item>-->
<!--      <el-form-item label="微信号" prop="wxno">-->
<!--        <el-input v-model="queryForm.wxno" placeholder="请输入微信号" size="small" clearable/>-->
<!--      </el-form-item>-->
<!--      <el-form-item label="开发进度" prop="status">-->
<!--        <el-select v-model="queryForm.status" placeholder="请选择开发进度" size="small" clearable>-->
<!--          <el-option v-for="dict in statusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>-->
<!--        </el-select>-->
<!--      </el-form-item>-->
      <el-form-item label="下单用户" prop="userId">
        <el-select v-model="queryForm.userId" placeholder="请选择下单用户" size="small" clearable>
          <el-option v-for="sysAuthor in sysAuthorDict" :key="sysAuthor.unionid" :value="sysAuthor.unionid" :label="sysAuthor.nickName"/>
        </el-select>
      </el-form-item>
      <el-form-item label="订单类型" prop="type">
        <el-select v-model="queryForm.type" placeholder="请选择订单类型" size="small" clearable>
          <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()">重置</el-button>
      </el-form-item>
    </el-form>
    <!-- 查询按钮 -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:'新增订单信息'})" v-hasPermi="['orders:add']">新增</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['orders:delete']">批量删除</el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <el-collapse accordion>
      <el-collapse-item style="background: #1c84c6" :title="'账单汇总(元)：'+count" name="1">
        <el-row>
          <el-col :span="12">
            <echarts-line style="width: 500px;height: 300px" ref="echartsLine"></echarts-line>
          </el-col>
          <el-col :span="12">
            <echarts-pie style="width: 500px;height: 300px" ref="echartsPie"></echarts-pie>
          </el-col>
        </el-row>
      </el-collapse-item>
    </el-collapse>
    <!-- 表格区域 -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="序号" type="index" fixed width="50"/>
<!--      <el-table-column label="订单编号" prop="orderNo" sortable/>-->
      <el-table-column label="项目名称" width="300" show-overflow-tooltip prop="name" sortable/>
      <el-table-column label="下单用户" prop="userId" sortable>
        <template slot-scope="scope">
          <span v-for="sysAuthor in sysAuthorDict" v-if="sysAuthor.unionid==scope.row.userId">{{sysAuthor.nickName}}</span>
        </template>
      </el-table-column>
      <el-table-column label="订单类型" prop="type" sortable>
        <template slot-scope="scope">
          <span :style="dict.style" v-for="dict in typeDict" v-if="dict.dictValue==scope.row.type">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="订单价格" prop="price" sortable/>
<!--      <el-table-column label="微信号" prop="wxno" sortable/>-->
      <el-table-column label="下单时间" prop="orderDate" sortable/>
      <el-table-column label="下单天数" prop="afterDay" sortable/>
      <el-table-column label="开发进度" prop="status" sortable>
        <template slot-scope="scope">
          <span :style="dict.style" v-for="dict in statusDict" v-if="dict.dictValue==scope.row.status">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="内容说明" prop="content" show-overflow-tooltip sortable/>
      <el-table-column label="操作" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'查看订单信息',pageFrom:'view',id:scope.row.id})" v-hasPermi="['orders:view']">查看</el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'修改订单信息',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['orders:edit']">修改</el-button>
          <el-button size="mini" type="success" round plain icon="el-icon-edit" @click="finishOrders(scope.row)"  v-hasPermi="['orders:delete']">结束订单</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页区域 -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import ordersForm from './ordersForm'
    export default {
      components: {ordersForm},
      data() {
        return {
          count:0,
          loading: true,//是否加载完成
          selectedData: [],//勾选数据
          total: 0, //记录数
          sysAuthorDict: [],
          dataList: [],
          queryForm: {
            current: 1,
            size: 10,
            orderNo: "",
            name: "",
            price: "",
            wxno: "",
            orderDate: "",
            status: "",
            userId: "",
            type: "",
            content: ""
          }
        };
      },
      mounted() {
        //初始化数据
        this.getList();
        //图表数据
        this.getData();
        //初始化【下单用户】
        this.getSysAuthorDict();
      },
      computed: {
        //【开发进度】
        statusDict() {
          return this.$store.getters.getDictArray('order_status');
        },
        //【订单类型】
        typeDict() {
          return this.$store.getters.getDictArray('order_type');
        },
      },
      methods: {
        //点击搜索
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //查询列表数据
        getList() {
          this.$request.doGet('/admin/orders/getPage?delFlag=0', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              this.dataList = res.obj.records;
              this.total = res.obj.total;
              this.getData(this.dataList);
            }
          });
        },
        //绘制柱状图
        getData(data){
          this.$request.doGet('/admin/orders/getData?delFlag=0', this.queryForm).then(res => {
            if (res.isOk) {
              this.$refs.echartsLine.drawLine(res.data.xData,res.data.yData)
              this.$refs.echartsPie.drawPie(res.data.data)
              this.count = res.data.count
            }
          });
        },
        //【下单用户】
        getSysAuthorDict() {
          this.$request.doGet('/admin/sysAuthor/getList').then(res => {
            if (res.isOk) {
              this.sysAuthorDict = res.list;
            }
          });
        },
        //重置查询
        resetQuery() {
          this.$refs['queryForm'].resetFields();
        },
        //打开弹窗
        openDialog(option) {
          this.$refs.ordersDialog.open(option);
        },
        //选择页数
        handleSizeChange: function (size) {
          this.queryForm.size = size;
          this.getList();
        },
        //选择页码
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
        //获取勾选数据
        handleSelectionChange(data) {
          this.selectedData = data;
        },
        //勾选批量删除
        handleDeleteAll() {
          const that = this;
          that.showConfirm('确定删除选中的数据吗?', function () {
            if (that.selectedData.length > 0) {
              that.selectedData.forEach(function (item, index) {
                that.delete(item.id);
              });
              that.showSuccess("删除成功");
            } else {
              that.showWarn("至少选择一条数据！");
            }
          });
        },
        //选中行删除
        handleDelete(id) {
          const that = this;
          that.showConfirm('确定删除选中的除数据吗?', function () {
            that.delete(id);
            that.showSuccess("删除成功");
          });
        },
        finishOrders(row){
          const that = this;
          that.showConfirm("确认结束该订单吗?", function () {
            row.delFlag = 1;
            that.$request.doPost('/admin/orders/sub', row).then(res => {
              if (res.isOk) {
                that.showSuccess("操作成功");
                that.getList();
              };
            });
          })
        },
        //删除方法
        delete(id) {
          this.$request.doGet('/admin/orders/delete?id=' + id).then(res => {
            if (res.isOk) {
              this.getList();
            }
          });
        },
      }
    };
</script>
<style scoped>

</style>



