<!-- 【媒体信息】弹窗页面 -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="media" ref="mediaForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="主键id" class="hidden" prop="id">
          <el-input type="hidden" v-model="media.id"/>
        </el-form-item>
        <el-form-item label="名称" prop="name">
          <el-input v-model="media.name" placeholder="请输入名称" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="备注" prop="remarks">
          <el-input type="textarea" v-model="media.remarks" placeholder="请输入备注" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="文件列表" prop="fileList">
          <sui-file type="media" :refId="media.id" v-model="media.fileList" v-if="dialog.open" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel">取 消</el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "mediaForm",
    data() {
      return {
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        media: {
          id: "",
          name: "",
          remarks: ""
        },
        //表单校验
        rules: {
          name: [{required: true, message: '请输入名称', trigger: 'blur'},{validator:this.$validator.unique,form:this,model:'media',tableName:'media',column:'name',message:'名称已存在',trigger:'blur'}],
          remarks: [{required: true, message: '请输入备注', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //打开弹窗
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getMedia(option.id);
        }
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false;
      },
      //根据id获取数据
      getMedia(id) {
        this.$request.doGet("/admin/media/get?id=" + id).then(res => {
          if (res.isOk) {
            this.media = res.obj;
          }
        });
      },
      //提交表单
      submitForm: function () {
        const that = this;
        that.$refs.mediaForm.validate((valid) => {
          if (valid) {//表单校验
            that.$request.doPost('/admin/media/sub', that.media).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.mediaForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      }
    }
  }
</script>

<style scoped>

</style>



