<!-- 【作品信息】弹窗页面 -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="works" ref="worksForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="主键id" class="hidden" prop="id">
          <el-input type="hidden" v-model="works.id"/>
        </el-form-item>
        <el-form-item label="作品图片" prop="picture">
          <sui-photo type="works" v-model="works.picture" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="作品名称" prop="name">
          <el-input v-model="works.name" placeholder="请输入作品名称" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="作品类型" prop="type">
          <el-select v-model="works.type" placeholder="请选择作品类型" :disabled="dialog.disabled" >
            <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="关键词" prop="keyWord">
          <el-input v-model="works.keyWord" placeholder="请输入关键词" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="价格" prop="price">
          <el-input v-model="works.price" placeholder="请输入价格" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="开发工具" prop="tools">
          <el-input v-model="works.tools" placeholder="请输入开发工具" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="部署环境" prop="environment">
          <el-input v-model="works.environment" placeholder="请输入部署环境" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="基本介绍" prop="introduce">
          <el-input type="textarea" v-model="works.introduce" placeholder="请输入基本介绍" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="文件列表" prop="fileList">
          <sui-file type="works" v-if="dialog.open" :refId="works.id" v-model="works.fileList" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel">取 消</el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "worksForm",
    data() {
      return {
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        works: {
          picture: "",
          name: "",
          type: "",
          keyWord: "",
          price: "",
          tools: "",
          environment: "",
          introduce: ""
        },
        //表单校验
        rules: {
          picture: [{required: true, message: '请选择作品图片', trigger: 'blur'}],
          name: [{required: true, message: '请输入作品名称', trigger: 'blur'}],
          type: [{required: true, message: '请选择作品类型', trigger: 'blur'}],
          keyWord: [{required: true, message: '请输入关键词', trigger: 'blur'}],
          price: [{required: true, message: '请输入价格', trigger: 'blur'}],
          tools: [{required: true, message: '请输入开发工具', trigger: 'blur'}],
          environment: [{required: true, message: '请输入部署环境', trigger: 'blur'}],
          introduce: [{required: true, message: '请输入基本介绍', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //【作品类型】
      typeDict() {
        return this.$store.getters.getDictArray('works_type');
      },
    },
    methods: {
      //打开弹窗
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getWorks(option.id);
        }
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false;
      },
      //根据id获取数据
      getWorks(id) {
        this.$request.doGet("/admin/works/get?id=" + id).then(res => {
          if (res.isOk) {
            this.works = res.obj;
          }
        });
      },
      //提交表单
      submitForm: function () {
        const that = this;
        that.$refs.worksForm.validate((valid) => {
          if (valid) {//表单校验
            that.$request.doPost('/admin/works/sub', that.works).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.worksForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      }
    }
  }
</script>

<style scoped>

</style>



